        </aside> -->
</template>

<script>
    
    import { mapMutations, mapActions, mapGetters } from 'vuex';
    export default {
        name: 'AsideNavBar',
        data() {
            return {
                path:  {
                    dashboard: '/',
                    utilizador:'utilizador',
                    cliente: '/cliente',
                    aula: '/aula',
                    configuracao: '/configuracao',
                    chat: '/chat',
                },
                icons: {
                        utilizador:'mdi:shield-user',
                        dashboard: 'iconamoon:home-fill',
                        cliente: 'ri:contacts-fill',
                        aula: 'icon-park-solid:classroom',
                        recibo: 'ion:receipt-sharp',
                        configuracao: 'material-symbols:settings',
                        chat: 'fluent:chat-24-filled',
                    },
                
                permissoes: [
                    {modulo:'dashboard',autorizacao: 'crud'},
                    {modulo:'aula',autorizacao: 'crud'},
                    {modulo:'chat',autorizacao: 'crud'},
                    {modulo:'configuracao',autorizacao: 'crud'},
                ]

            }
        },

        computed: {
            ...mapGetters(['getLoggedUser','getPermissao']),
        },

        methods: {
            // ...mapActions(['fethPermissoes']),
            ...mapMutations(['setLoggedUser']),

            customModulo(modulo){

                if(modulo === 'configuracao'){
                    return 'configuração'
                }

                else{
                    return modulo
                }
            },
                        
        },

        async created() {
            
            this.setLoggedUser()
            await this.$store.dispatch('fethPermissoes', this.getLoggedUser.id)

            // this.permissoes = this.getPermissao


            // this.permissoes.push({modulo:'recibo',autorizacao: 'crud'})


            console.log('User: ', this.getLoggedUser);
            console.log('Permissões no aside: ', this.permissoes);
            
            
        },
        
    }
</script>

